import React from "react"
import Layout from "../components/layout"
import { graphql } from "gatsby"
import { rhythm } from "../utils/typography"
import { TagList } from "../components/tags"
import SEO from "../components/seo"

function Projects({ data, location }) {
  return (
    <Layout location={location}>
      <SEO title="Projects"/>
      <h1>Github projects</h1>
      <p>Here are my public projects on <a href={data.github.viewer.url}>Github</a>.</p>
      {data.github.viewer.repositories.edges.map(({ node }) => (
        <div key={node.name}>
          <h3
            style={{
              marginBottom: rhythm(1 / 4),
            }}
          >
            <a href={node.url} target="_blank" rel="noopener noreferrer">{node.name}</a>
          </h3>
          <p>{node.description}</p>
          <TagList tags={[node.primaryLanguage.name.toLowerCase(), node.name]}/>
        </div>
      ))}
    </Layout>
  )
}

export default Projects

export const projectsQuery = graphql`
  query {
    github {
      viewer {
        name
        url
        repositories(first: 20, privacy: PUBLIC, orderBy: {
          field: STARGAZERS,
          direction: DESC
        }) {
          edges {
            node {
              name
              description
              url
              primaryLanguage {
                name
              }
            }
          }
        }
      }
    }
  }
`